              (value?.length && value.some(entry => entry.value))
          },
          $each: {
            value: {
              [L('Non-monetary pledge cannot exceed {maxChars} characters', {
                maxChars: GROUP_NON_MONETARY_CONTRIBUTION_MAX_CHAR
              })]: maxLength(GROUP_NON_MONETARY_CONTRIBUTION_MAX_CHAR)
            }
          }
        }
      }
    }
  }
}

